import React, { useState } from 'react';
import { Select, InputNumber, Card, Typography } from 'antd';
import styled from 'styled-components';

const { Title } = Typography;

const Container = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
`;

const FormItem = styled.div`
  margin-bottom: 20px;
  display: flex;
  align-items: center;
`;

const Label = styled.div`
  width: 200px;
  margin-right: 20px;
  font-weight: bold;
`;

const ValueDisplay = styled.div`
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 24px;
`;

const App: React.FC = () => {
  const [companyType, setCompanyType] = useState<string>('非周期性行业');
  const [cashFlowType, setCashFlowType] = useState<string>('归母净利润');
  const [freeCashFlow, setFreeCashFlow] = useState<number>(0);
  const [growthRate, setGrowthRate] = useState<number>(10);
  const [minPE, setMinPE] = useState<number>(25);
  const [maxPE, setMaxPE] = useState<number>(30);

  const calculateLowValue = () => {
    const pe = minPE;
    const growth = (1 + growthRate / 100) ** 3;
    const peWithGrowth = Math.min(pe * growth / 2, pe * 0.7);
    return (freeCashFlow * peWithGrowth).toFixed(2);
  };

  const calculateHighValue = () => {
    return (freeCashFlow * 50).toFixed(2);
  };

  return (
    <Container>
      <Title level={2}>股票估值计算器</Title>
      <Card>
        <FormItem>
          <Label>企业类型</Label>
          <Select
            style={{ width: 200 }}
            value={companyType}
            onChange={setCompanyType}
            options={[
              { value: '非周期性行业', label: '非周期性行业' },
            ]}
          />
        </FormItem>

        <FormItem>
          <Label>自由现金流</Label>
          <Select
            style={{ width: 200 }}
            value={cashFlowType}
            onChange={setCashFlowType}
            options={[
              { value: '归母净利润', label: '归母净利润' },
              { value: '非国际净利润', label: '非国际净利润' },
            ]}
          />
        </FormItem>

        <FormItem>
          <Label>现金流数值（亿元）</Label>
          <InputNumber
            style={{ width: 200 }}
            value={freeCashFlow}
            onChange={(value) => setFreeCashFlow(value || 0)}
          />
        </FormItem>

        <FormItem>
          <Label>未来三年年均增长率 (%)</Label>
          <InputNumber
            style={{ width: 200 }}
            value={growthRate}
            onChange={(value) => setGrowthRate(value || 0)}
            min={10}
            max={20}
          />
        </FormItem>

        <FormItem>
          <Label>合理市盈率范围</Label>
          <InputNumber
            style={{ width: 95 }}
            value={minPE}
            onChange={(value) => setMinPE(value || 0)}
          />
          <span style={{ margin: '0 10px' }}>~</span>
          <InputNumber
            style={{ width: 95 }}
            value={maxPE}
            onChange={(value) => setMaxPE(value || 0)}
          />
        </FormItem>

        <ValueDisplay>
          <div>
            <Title level={4}>理想低估值（市值亿元）</Title>
            <div style={{ color: '#ff4d4f', fontSize: '20px' }}>
              {calculateLowValue()}
            </div>
          </div>
          <div>
            <Title level={4}>一年内高估值（市值亿元）</Title>
            <div style={{ color: '#52c41a', fontSize: '20px' }}>
              {calculateHighValue()}
            </div>
          </div>
          <div>
            <Label>理想低估值：自由现金流Min *（Min（合理市盈率） * （1 + 未来三年年均增长）^ 3, Min（合理市盈率）* 0.7）</Label>
            <Label>一年内高估值：自由现金流 * 50 </Label>
          </div>
        </ValueDisplay>
      </Card>
    </Container>
  );
};

export default App; 